<template>
    <div>
        <router-view></router-view>
        <ul>
            <li v-for="(item,index) in goodsList" :key="index">
                <router-link :to="{name:'details',params:{name:item.goodsName,id:item.id}}">{{item.goodsName}}</router-link>
            </li>
        </ul>
    </div>
</template>

<script>
import Vuex from "vuex";
export default{
    created(){
        console.log(this);
    },
    data(){
        return {
            goodsList:[
                {
                    id:1,
                    goodsName:"马蕊"
                },
                {
                    id:2,
                    goodsName:"张三"
                },
                {
                    id:3,
                    goodsName:"李四"
                },
                {
                    id:4,
                    goodsName:"王五"
                }
            ]
        }
    },
    computed:{
        ...Vuex.mapState({
            a:state=>state.homeStore.a
        })
    },
    methods:{
        ...Vuex.mapActions({
            fn:"homeStore/fn"
        })
    }
}


