import Vue from 'vue'
import App from './App.vue'
import router from './router/router'
// import store from './store'

// Vue.config.productionTip = false

new Vue({
        router,
        render: h => h(App)
    }).$mount('#app')
    /**
     * keep-alive:组件的缓存
     * 如果当前组件需要进行缓存的情况下，我们需要将当前组件外部加一个keep-alive；
     * activated:活跃状态 当前组件是一个显示的状态
     * deactivated：缓存状态 如果组件进行了切换 那么当前组件就会保存在缓存当中
     * include:包含 包含哪些组件可以进入缓存状态
     * exclude:排除 排除哪些组件可以进入缓存状态
     * 尽量配合component进行使用
     * router-view也可以进行使用但是exclude include 可能不会生效
     * 
     */