<template>
    <div>
        <h2>接收到的宝贝为:{{name}}----------宝贝id为:{{id}}</h2>
    </div>
</template>
<script>
export default {
    // created(){
    //     console.log(this.$route.params)
    //     this.id = this.$route.params.id;
    //     this.name = this.$route.params.name
    // },
    // props:["id","name"],
    data(){
        return {
            name:"",
            id:""
        }
    },
    // watch:{
    //     "$route"(newVal,oldVal){
    //         this.id = newVal.params.id;
    //         this.name = newVal.params.name;
    //     }
    // },
    // //当路由方法改变的时候监听路由的变化
    beforeRouteUpdate(to,from,next){
        console.log(to);
        this.id = to.params.id;
        this.name = to.params.name;
        next();
    }
}
/*
    什么时候会用到路由的监听 或者 beforRouteUpdate

    当当前组件被复用的时候(没有经历创建和销毁的时候)，当路由发生改变的时候数据是不会发生改变的  需要调用
    beforeRouteUpdate或者watch进行监听 



    路由解耦：
        在路由的配置项中加一个props设置为true

        在需要接受路由参数的组件中通过props进行接受  例如 props:["属性","属性"]


*/
</script>



